import { TeaBlocksError } from './../../../TeaBlocksError';
import { ReturnBlock } from "../../ReturnBlock";

/**
 * Math exponent block. Does input[0]^input[1]^input[2]...
 */
export class ExponentBlock extends ReturnBlock {
    inputs;

    /**
     * Constructor
     * @param inputs the left operand
     */
    constructor(id: string, inputs: ReturnBlock[]) {
        super(id)
        this.inputs = inputs;
    }

    /**
     * Evaluates the expression
     * @param {Map} varEnv variable environment
     */
    evalBlock(varEnv: Map<string, any>): number {
        let firstNum = Number(this.inputs[0].evalBlock(varEnv));

        if (isNaN(firstNum)) throw new TeaBlocksError('Inputs must be numbers', this.id);

        if (this.inputs.length === 1 || firstNum === 1 || firstNum === 0) return firstNum;

        this.inputs.slice(1).forEach(inp => {
            let pow = Number(inp.evalBlock(varEnv));
            if (isNaN(pow)) throw new TeaBlocksError('Inputs must be numbers', this.id);
            if (pow === 0) return 1;
            firstNum = Math.pow(firstNum, pow);
        });

        return firstNum;
    }
}
