import { ReturnBlock } from "../../../ReturnBlock";

/**
 * Boolean AND operation
 */
export class ANDBlock extends ReturnBlock {
    inputs;

    /**
     * Constructor
     * @param inputs the inputs being processed
     */
    constructor(id: string, inputs: ReturnBlock[]) {
        super(id)
        this.inputs = inputs;
    }

    /**
     * Performs boolean AND operation on inputs
     * @param varEnv the variable envornment
     */
    evalBlock(varEnv: Map<string, any>): boolean {
        for(let inp of this.inputs){
            let val = Boolean(inp.evalBlock(varEnv));
            if (!val){
                return false;
            }
        }

        return true;
    }
}