import { TeaBlocksError } from './../../TeaBlocksError';
import { ReturnBlock } from "../ReturnBlock";
import { VoidBlock } from "../VoidBlock";
import { Shape } from "./Shape";

/**
 * Block for rotating a shape (by degrees).
 */
export class RotateBlock extends VoidBlock {
    shape: ReturnBlock; // a GetShapeBlock, tho madlads can use constants that start with $.
    rot: ReturnBlock;

    /**
     * Constructor
     * @param inputs the inputs (shape is inputs[0]; rotation is inputs[1])
     */
    constructor(id: string, inputs: ReturnBlock[]) {
        super(id)
        this.shape = inputs[0];
        this.rot = inputs[1];
    }

    /**
     * Adds rot to the rotation of the shape.
     * @param varEnv variable environment
     */
    *evalBlock(varEnv: Map<string, any>) {
        let shapeName: string = String(this.shape.evalBlock(varEnv));
        let shape: Shape = varEnv.get(shapeName);

        if(!shape) throw new TeaBlocksError('This shape does not exist', this.id);

        let rot = Number(this.rot.evalBlock(varEnv));

        if (isNaN(rot)) {
            throw new TeaBlocksError("Input not a number", this.id);
        }

        shape.rot += rot;

        varEnv.set(shapeName, shape);

        yield;
    }


}