import { TeaBlocksError } from './../../../TeaBlocksError';
import { ReturnBlock } from "../../ReturnBlock";

/**
 * Math division block. Does input[0]/input[1]/input[2]... if all values after input[0] are non zero.
 */
export class DivisionBlock extends ReturnBlock {
    inputs;

    /**
     * Constructor
     * @param inputs the inputs.
     */
    constructor(id: string, inputs: ReturnBlock[]) {
        super(id)
        this.inputs = inputs;
    }

    /**
     * Evaluates the expression.
     * @param varEnv the variable environment
     */
    evalBlock(varEnv: Map<string, any>): number {
        let rVal = Number(this.inputs[0].evalBlock(varEnv));
        if (this.inputs.length === 1 || rVal === 0 || isNaN(rVal)) {
            return rVal;
        }

        this.inputs.slice(1).forEach(inp => {
            let num = Number(inp.evalBlock(varEnv));
            if(isNaN(num)) throw new TeaBlocksError('Input is not a number', this.id);

            if (num === 0) {
                throw new TeaBlocksError('Can\'t divide by zero.', this.id);
            }
            rVal /= num;
        });

        return rVal;
    }
}
