import { Button } from 'beautiful-react-ui';
import '../../styles/SpecialRender.scss';
import DiagramIDE from '../../ide/DiagramIDE';
import { RenderProps } from './RenderProps';
import { RenderHelper } from './RenderHelper';

export function WhileRender(props: RenderProps) {
    let inpDivs = props.inputs ? RenderHelper.makeInputDivs(props.inputs, props.data.inputNames) : [];

    function loopButton() {
        console.log("Clicked loop button!");
        DiagramIDE.getIDE().setDisplaySchemaId(`${props.id}while`);
    }

    return (
        <div>
            <div className="while-node">
                <div className="deleteBtn">
                    <Button icon="times" size="small" onClick={() => RenderHelper.deleteNode(props.data.parentSchema, props.id)} />
                </div>
                <div className="ports">
                    <div className="inputs">
                        {inpDivs}
                    </div>
                    <div className="content">
                        {props.content}
                    </div>
                </div>
                <div className="buttons">
                    <button className="loop-btn" onClick={loopButton}>Loop</button>
                </div>
            </div>
        </div>
    )
}

